'use client'; // Next.js Client Component

import React, { useState, useLayoutEffect } from 'react';
import SecurityAnalysis from './SecurityAnalysis';
import './style.css';

var themeMode = 0;

const hour = new Date().getHours();
if (hour >= 19 || hour < 7) {
  themeMode = 2;
} else {
  themeMode = 1;
}

// Function to set theme based on time
const setThemeBasedOnTime = () => {
  const hour = new Date().getHours();
  const body = document.body;

  function getAllElements(element = document.documentElement, elements = []) {
    elements.push(element); // Add the current element to the list
    element = element.firstElementChild; // Start with the first child

    // Recursively process all children
    while (element) {
      getAllElements(element, elements);
      element = element.nextElementSibling; // Move to the next sibling
    }

    // toggleMode();

    return elements;
  }

  // Usage
  const allElements = getAllElements();
  const sections = allElements.filter(el => el.tagName.toLowerCase() === 'section' || el.tagName.toLowerCase() === 'p' ||
    el.tagName.toLowerCase() === 'strong' || el.tagName.toLowerCase() === 'button' || el.tagName.toLowerCase() === 'img'
     || el.tagName.toLowerCase() === 'input' || el.tagName.toLowerCase() === 'select' || el.tagName.toLowerCase() === 'textarea'); // Select all section elements

  // Consider dark mode from 7 PM (19:00) to 7 AM (07:00)
  if (themeMode == 1) {
    body.classList.remove('dark-mode');
    sections.forEach(section => section.classList.remove('dark-mode')); // Remove dark-mode from each section
  } else if (themeMode == 2) {
    body.classList.add('dark-mode');
    sections.forEach(section => section.classList.add('dark-mode'));
  } else {
    if (hour >= 19 || hour < 7) {
      themeMode = 2;
      body.classList.add('dark-mode');
      sections.forEach(section => section.classList.add('dark-mode'));
    } else {
      themeMode = 1;
      body.classList.remove('dark-mode');
      sections.forEach(section => section.classList.remove('dark-mode')); // Remove dark-mode from each section
    }
  }
};

export function App() {
  const [isVisible, setIsVisible] = useState(false);
  const [isLightMode, setIsLightMode] = useState(false);
  const [activeMenu, setActiveMenu] = useState('home'); // State to manage the active menu

  // Function to toggle between light and dark modes
  const toggleMode = () => {
    setIsLightMode((prevMode) => !prevMode);
  };

  useLayoutEffect(() => {

    const toggleVisibility = () => {
      if (window.pageYOffset > 300) {
        setIsVisible(true);
      } else {
        setIsVisible(false);
      }
    };

    window.addEventListener('scroll', toggleVisibility);

    // Call the function to set the theme after layout
    setThemeBasedOnTime();

    setTimeout(() => {
        const menuToggle = document.querySelector(".menu-toggle");
        const navMenu = document.querySelector("nav");

        menuToggle.addEventListener("click", function () {
          navMenu.classList.toggle("open");
        });

    }, 500);

    return () => window.removeEventListener('scroll', toggleVisibility);

  }, []); // Empty dependency array to ensure it runs only once after the initial render

  const scrollToTop = () => {
    window.scrollTo({
      top: 0,
      behavior: 'smooth',
    });
  };

  // Function to handle menu item clicks
  const handleMenuClick = (menu) => {
    setActiveMenu(menu);
    const navMenu = document.querySelector("nav");
    navMenu.classList.toggle("open");
  };

  // Function to render the appropriate content based on the active menu
  const renderContent = () => {
    setTimeout(() => {
        switchTheme();
        switchTheme();
    }, 200);
    switch (activeMenu) {
      case 'home':
        return <MainContent />;
      case 'security-analysis':
        return <SecurityAnalysis />;
      default:
        return <MainContent />;
    }
  };

  return (
    <div className='App'>
      <Header onMenuClick={handleMenuClick} />
      <main>
        {renderContent()}
      </main>
      <div className="scrollup">
        {isVisible && (
          <button onClick={scrollToTop} className="scroll-to-top">
            <img className="arrow"
              src="arrow.png" // Replace with your icon path
              alt="Scroll to top"
            />
          </button>
        )}
      </div>
      <Footer />
    </div>
  );
}

