const getAllElements = (element = document.documentElement, elements = []) => {
    elements.push(element); // Add the current element to the list
    element = element.firstElementChild; // Start with the first child

    // Recursively process all children
    while (element) {
      getAllElements(element, elements);
      element = element.nextElementSibling; // Move to the next sibling
    }

    return elements;
  }

  // Usage
  const bind = () => {
    const allElements = getAllElements();
    const sections = allElements.filter(el => el.tagName.toLowerCase() === 'p'); // Select all section elements
    const body = document.body;

    setThemeBasedOnTime();

    // Apply dark mode based on the time
    if (themeMode == 1) {
      body.classList.remove('dark-mode');
      sections.forEach(section => section.classList.remove('dark-mode')); // Remove dark-mode from each section
    } else if (themeMode == 2) {
      body.classList.add('dark-mode');
      sections.forEach(section => section.classList.add('dark-mode'));
    } else {
      if (hour >= 19 || hour < 7) {
          themeMode = 2;
          body.classList.add('dark-mode');
          sections.forEach(section => section.classList.add('dark-mode'));
      } else {
          themeMode = 1;
          body.classList.remove('dark-mode');
          sections.forEach(section => section.classList.remove('dark-mode'));
      }
    }
  }

  const switchTheme = () => {
    if (themeMode == 2) themeMode = 1;
    else if (themeMode == 1) themeMode = 2;
    else themeMode = 1;
    bind();
  }

const ThemeSwitcher = () => {

    // Function to set theme based on time
    const setThemeBasedOnTime = () => {
        const hour = new Date().getHours();
        const body = document.body;

        // Function to get all section elements
        const getAllSections = (element = document.documentElement, elements = []) => {
            if (element.tagName === 'SECTION') {
                elements.push(element); // Add the section element to the list
            }
            element = element.firstElementChild; // Start with the first child

            // Recursively process all children
            while (element) {
                getAllSections(element, elements);
                element = element.nextElementSibling; // Move to the next sibling
            }

            return elements;
        };

        // Get all section elements
        const sections = getAllSections();

        // Apply dark mode based on the time
        if (themeMode == 1) {
          body.classList.remove('dark-mode');
          sections.forEach(section => section.classList.remove('dark-mode')); // Remove dark-mode from each section
        } else if (themeMode == 2) {
          body.classList.add('dark-mode');
          sections.forEach(section => section.classList.add('dark-mode'));
        } else {
          if (hour >= 19 || hour < 7) {
              themeMode = 2;
              body.classList.add('dark-mode');
              sections.forEach(section => section.classList.add('dark-mode'));
          } else {
              themeMode = 1;
              body.classList.remove('dark-mode');
              sections.forEach(section => section.classList.remove('dark-mode'));
          }
        }
    };

    
    return null; // This component does not render anything
};

